// src/hooks/useDataLoaders.ts
import { useCallback, useMemo, useRef } from "react";
import type { SetStateAction } from "react";
import type {
  Crew,
  Location,
  Requirement,
  Signoff,
  Track,
  Training,
  TrainingGroup,
  TrainingRecord,
} from "../types/domain";
import { useLocation } from "../context/LocationContext";

type SetState<T> = (value: SetStateAction<T>) => void;
type SetRows<T> = (rows: T[]) => void;

type SupabaseGet = (path: string, opts?: any) => Promise<any>;

type LoadListArgs<Row, Mapped> = {
  key?: string;
  force?: boolean;
  setLoading: SetState<boolean>;
  setError: SetState<string>;
  path: string;
  mapRow: (row: Row) => Mapped;
  setRows: SetRows<Mapped>;
  useLocationFilter?: boolean;
  useCache?: boolean;
  silent?: boolean;
};

type LoadOptions = { silent?: boolean };

type UseDataLoadersParams = {
  supabaseGet: SupabaseGet;
  safeSet: (setter: () => void) => void;
  refreshMs?: number;

  // Locations
  setLocations: SetState<any[]>;
  setLocationsLoading: SetState<boolean>;
  setLocationsError: SetState<string>;

  // Departments
  setDepartments: SetState<any[]>;
  setDepartmentsLoading: SetState<boolean>;
  setDepartmentsError: SetState<string>;
  setDepartmentsFromDb: SetState<boolean>;

  // Crew
  setCrew: SetState<any[]>;
  setCrewLoading: SetState<boolean>;
  setCrewError: SetState<string>;

  // Tracks
  setTracks: SetState<any[]>;
  setTracksLoading: SetState<boolean>;
  setTracksError: SetState<string>;

  // Trainings
  setTrainings: SetState<any[]>;
  setTrainingsLoading: SetState<boolean>;
  setTrainingsError: SetState<string>;

  // Training Groups
  setTrainingGroups: SetState<any[]>;
  setTrainingGroupsLoading: SetState<boolean>;
  setTrainingGroupsError: SetState<string>;

  // Requirements
  setRequirements: SetState<any[]>;
  setRequirementsLoading: SetState<boolean>;
  setRequirementsError: SetState<string>;

  // Signoffs
  setSignoffs: SetState<any[]>;
  setSignoffsLoading: SetState<boolean>;
  setSignoffsError: SetState<string>;

  // Records
  setTrainingRecords: SetState<any[]>;
  setRecordsLoading: SetState<boolean>;
  setRecordsError: SetState<string>;
};

type LocationRow = Location;

type DepartmentRow = {
  id: number;
  department_name: string;
  is_department_active?: boolean | null;
  location_id?: number;
};

type CrewRow = {
  id: number;
  crew_name: string;
  home_department: string;
  status: string;
  is_department_lead?: boolean | string | number | null;
  weekly_off_day_1?: number | null;
  weekly_off_day_2?: number | null;
  location_id?: number;
};

type TrackRow = {
  id: number;
  local_id: number | null;
  track_name: string;
  is_track_active?: boolean | null;
  is_show_critical?: boolean | null;
  track_color?: string | null;
  location_id?: number;
};

type TrainingGroupRow = {
  id: number;
  local_id: number | null;
  name: string;
  active?: boolean | null;
  sort_order?: number | null;
  color?: string | null;
  description?: string | null;
  location_id?: number;
};

type TrainingRow = {
  id: number;
  local_id: number | null;
  location_id?: number;
  training_name: string;
  is_training_active?: boolean | null;
  expires_after_weeks?: number | null;
  training_group_id?: number | null;
};

type RequirementRow = {
  id: number;
  track_id: number;
  training_id: number;
  is_requirement_active?: boolean | null;
  location_id?: number;
};

type SignoffRow = {
  id: number;
  crew_id: number;
  track_id: number;
  signoff_status: string;
  is_signoff_active?: boolean | null;
  location_id?: number;
};

type TrainingRecordRow = {
  id: number;
  crew_id: number;
  track_id: number;
  training_id: number;
  is_record_active?: boolean | null;
  last_completed?: string | null;
  training_status?: string | null;
  due_date?: string | null;
  days_until_due?: number | null;
  days_overdue?: number | null;
  crew_name?: string | null;
  home_department?: string | null;
  crew_status?: string | null;
  track_name?: string | null;
  training_name?: string | null;
  last_signed_off_by?: string | null;
  last_signed_off_on?: string | null;
  location_id?: number | null;
};


function getErrorMessage(e: unknown) {
  return e instanceof Error ? e.message : String(e);
}

function isTruthyFlag(value: unknown) {
  return value === true || value === "true" || value === "t" || value === 1;
}

export function useDataLoaders({
  supabaseGet,
  safeSet,
  refreshMs = 10000,

  // Locations
  setLocations,
  setLocationsLoading,
  setLocationsError,

  // Departments
  setDepartments,
  setDepartmentsLoading,
  setDepartmentsError,
  setDepartmentsFromDb,

  // Crew
  setCrew,
  setCrewLoading,
  setCrewError,

  // Tracks
  setTracks,
  setTracksLoading,
  setTracksError,

  // Trainings
  setTrainings,
  setTrainingsLoading,
  setTrainingsError,

  // Training Groups
  setTrainingGroups,
  setTrainingGroupsLoading,
  setTrainingGroupsError,

  // Requirements
  setRequirements,
  setRequirementsLoading,
  setRequirementsError,

  // Signoffs
  setSignoffs,
  setSignoffsLoading,
  setSignoffsError,

  // Records
  setTrainingRecords,
  setRecordsLoading,
  setRecordsError,
}: UseDataLoadersParams) {
  const { activeLocationId, setActiveLocationId, cacheTag, withLoc } =
    useLocation();

  const lastFetchRef = useRef<Record<string, number>>({
    locations: 0,
    departments: 0,
    crew: 0,
    trackDefs: 0,
    trainingDefs: 0,
    trainingGroups: 0,
    requirements: 0,
    signoffs: 0,
    records: 0,
  });

  const shouldFetch = useCallback(
    (key: string) => {
      const now = Date.now();
      return now - (lastFetchRef.current[key] || 0) > refreshMs;
    },
    [refreshMs]
  );

  const markFetched = useCallback((key: string) => {
    lastFetchRef.current[key] = Date.now();
  }, []);

  const opts = useMemo(() => ({ cacheTag }), [cacheTag]);

  const loadList = useCallback(
    async <Row, Mapped>({
      key,
      force = false,
      setLoading,
      setError,
      path,
      mapRow,
      setRows,
      useLocationFilter = true,
      useCache = true,
      silent = false,
    }: LoadListArgs<Row, Mapped>) => {
      if (key && !force && !shouldFetch(key)) return;

      if (!silent) {
        safeSet(() => {
          setLoading(true);
          setError("");
        });
      }

      try {
        const finalPath = useLocationFilter ? withLoc(path) : path;
        const data = await supabaseGet(finalPath, useCache ? opts : undefined);

        safeSet(() => setRows((data || []).map(mapRow)));

        if (key) markFetched(key);
      } catch (e) {
        if (!silent) {
          safeSet(() => setError(getErrorMessage(e)));
        } else {
          // Keep UI stable during background refresh failures
          console.warn("Background refresh failed", e);
        }
      } finally {
        if (!silent) {
          safeSet(() => setLoading(false));
        }
      }
    },
    [markFetched, opts, safeSet, shouldFetch, supabaseGet, withLoc]
  );

  const loadLocations = useCallback(
    async (force = false, opts: LoadOptions = {}) => {
      await loadList({
        key: "locations",
        force,
        setLoading: setLocationsLoading,
        setError: setLocationsError,
        path: "/rest/v1/locations?select=id,code,name,active&order=name.asc",
        mapRow: (l: LocationRow) => l,
        setRows: (rows: LocationRow[]) => {
          const active = (rows || []).filter((l) => l.active !== false);

          setLocations(active);

          if (!activeLocationId && active[0]?.id != null) {
            const firstId = Number(active[0].id);
            setActiveLocationId(firstId);
            localStorage.setItem("blueprint_location_id", String(firstId));
          }
        },
        useLocationFilter: false,
        useCache: false,
        silent: opts.silent,
      });
    },
    [
      activeLocationId,
      loadList,
      setActiveLocationId,
      setLocations,
      setLocationsError,
      setLocationsLoading,
    ]
  );

  const loadDepartments = useCallback(
    async (force = false, opts: LoadOptions = {}) => {
      await loadList({
        key: "departments",
        force,
        setLoading: setDepartmentsLoading,
        setError: setDepartmentsError,
        path: "/rest/v1/department_definitions?select=id,department_name,is_department_active,location_id&order=department_name.asc",
        mapRow: (d: DepartmentRow) => ({
          id: d.id,
          name: d.department_name,
          active: d.is_department_active ?? true,
        }),
        setRows: (rows: Array<{ id: number; name: string; active: boolean }>) => {
          const cleaned = (rows || [])
            .filter((r) => r.active !== false)
            .map((r) => ({
              id: r.id,
              name: String(r.name || "").trim(),
              active: r.active !== false,
            }))
            .filter((r) => r.name);

          setDepartments(cleaned);
          setDepartmentsFromDb(true);
        },
        silent: opts.silent,
      });
    },
    [
      loadList,
      setDepartments,
      setDepartmentsError,
      setDepartmentsFromDb,
      setDepartmentsLoading,
    ]
  );

  const loadCrew = useCallback(
    async (force = false, opts: LoadOptions = {}) => {
      if (!force && !shouldFetch("crew")) return;

      if (!opts.silent) {
        safeSet(() => {
          setCrewLoading(true);
          setCrewError("");
        });
      }

      const withLeadPath =
        "/rest/v1/crew_roster?select=id,crew_name,home_department,status,is_department_lead,weekly_off_day_1,weekly_off_day_2,location_id&order=crew_name.asc";
      const withLeadOnlyPath =
        "/rest/v1/crew_roster?select=id,crew_name,home_department,status,is_department_lead,location_id&order=crew_name.asc";
      const legacyPath =
        "/rest/v1/crew_roster?select=id,crew_name,home_department,status,location_id&order=crew_name.asc";

      try {
        let rows: CrewRow[] = [];
        try {
          rows = await supabaseGet(withLoc(withLeadPath), cacheTag ? { cacheTag } : undefined);
        } catch (firstErr) {
          const msg = getErrorMessage(firstErr).toLowerCase();
          const missingWeeklyCols =
            msg.includes("weekly_off_day_1") || msg.includes("weekly_off_day_2");
          const missingLeadCol = msg.includes("is_department_lead");
          const missingColumn = msg.includes("42703") || msg.includes("column");
          if (!missingColumn && !missingLeadCol && !missingWeeklyCols) throw firstErr;

          if (missingWeeklyCols && !missingLeadCol) {
            rows = await supabaseGet(
              withLoc(withLeadOnlyPath),
              cacheTag ? { cacheTag } : undefined
            );
          } else {
            rows = await supabaseGet(
              withLoc(legacyPath),
              cacheTag ? { cacheTag } : undefined
            );
          }
        }

        safeSet(() =>
          setCrew(
            (rows || []).map((c: CrewRow): Crew => ({
              id: c.id,
              name: c.crew_name,
              dept: c.home_department,
              active: c.status === "Active",
              isDepartmentLead: isTruthyFlag(c.is_department_lead),
              weeklyOffDay1:
                c.weekly_off_day_1 == null
                  ? null
                  : Number.isFinite(Number(c.weekly_off_day_1))
                  ? Number(c.weekly_off_day_1)
                  : null,
              weeklyOffDay2:
                c.weekly_off_day_2 == null
                  ? null
                  : Number.isFinite(Number(c.weekly_off_day_2))
                  ? Number(c.weekly_off_day_2)
                  : null,
              statusRaw: c.status,
            }))
          )
        );

        markFetched("crew");
      } catch (e) {
        if (!opts.silent) {
          safeSet(() => setCrewError(getErrorMessage(e)));
        } else {
          console.warn("Background refresh failed", e);
        }
      } finally {
        if (!opts.silent) {
          safeSet(() => setCrewLoading(false));
        }
      }
    },
    [
      cacheTag,
      markFetched,
      safeSet,
      setCrew,
      setCrewError,
      setCrewLoading,
      shouldFetch,
      supabaseGet,
      withLoc,
    ]
  );

  const loadTracks = useCallback(
    async (force = false, opts: LoadOptions = {}) => {
      if (!force && !shouldFetch("trackDefs")) return;

      if (!opts.silent) {
        safeSet(() => {
          setTracksLoading(true);
          setTracksError("");
        });
      }

      const withCriticalPath =
        "/rest/v1/track_definitions?select=id,local_id,track_name,is_track_active,is_show_critical,track_color,location_id&order=local_id.asc";
      const legacyPath =
        "/rest/v1/track_definitions?select=id,local_id,track_name,is_track_active,track_color,location_id&order=local_id.asc";

      try {
        let rows: TrackRow[] = [];
        try {
          rows = await supabaseGet(withLoc(withCriticalPath), cacheTag ? { cacheTag } : undefined);
        } catch (firstErr) {
          const msg = getErrorMessage(firstErr).toLowerCase();
          const missingCriticalCol =
            msg.includes("is_show_critical") ||
            msg.includes("42703") ||
            msg.includes("column");
          if (!missingCriticalCol) throw firstErr;

          rows = await supabaseGet(withLoc(legacyPath), cacheTag ? { cacheTag } : undefined);
        }

        safeSet(() =>
          setTracks(
            (rows || []).map((t: TrackRow): Track => ({
              id: t.id,
              localId: t.local_id,
              name: t.track_name,
              active: !!t.is_track_active,
              showCritical: t.is_show_critical === true,
              color: (t.track_color || "").trim(),
            }))
          )
        );

        markFetched("trackDefs");
      } catch (e) {
        if (!opts.silent) {
          safeSet(() => setTracksError(getErrorMessage(e)));
        } else {
          console.warn("Background refresh failed", e);
        }
      } finally {
        if (!opts.silent) {
          safeSet(() => setTracksLoading(false));
        }
      }
    },
    [
      cacheTag,
      markFetched,
      safeSet,
      setTracks,
      setTracksError,
      setTracksLoading,
      shouldFetch,
      supabaseGet,
      withLoc,
    ]
  );

  const loadTrainingGroups = useCallback(
    async (force = false, opts: LoadOptions = {}) => {
      await loadList({
        key: "trainingGroups",
        force,
        setLoading: setTrainingGroupsLoading,
        setError: setTrainingGroupsError,
        path: "/rest/v1/training_groups?select=id,local_id,name,active,sort_order,color,description,location_id&order=sort_order.asc,name.asc",
        mapRow: (g: TrainingGroupRow): TrainingGroup => ({
          id: g.id,
          localId: g.local_id,
          name: g.name,
          active: g.active !== false,
          sortOrder: g.sort_order,
          color: (g.color || "").trim(),
          description: (g.description || "").trim(),
        }),
        setRows: setTrainingGroups,
        silent: opts.silent,
      });
    },
    [
      loadList,
      setTrainingGroups,
      setTrainingGroupsError,
      setTrainingGroupsLoading,
    ]
  );

  const loadTrainings = useCallback(
    async (force = false, opts: LoadOptions = {}) => {
      await loadList({
        key: "trainingDefs",
        force,
        setLoading: setTrainingsLoading,
        setError: setTrainingsError,
        path: "/rest/v1/training_definitions?select=id,local_id,location_id,training_name,is_training_active,expires_after_weeks,training_group_id&order=local_id.asc",
        mapRow: (t: TrainingRow): Training => ({
          id: t.id,
          localId: t.local_id,
          name: t.training_name,
          active: !!t.is_training_active,
          expiresAfterWeeks:
            t.expires_after_weeks == null
              ? null
              : Number(t.expires_after_weeks),
          trainingGroupId:
            t.training_group_id == null ? null : Number(t.training_group_id),
        }),
        setRows: setTrainings,
        silent: opts.silent,
      });
    },
    [loadList, setTrainings, setTrainingsError, setTrainingsLoading]
  );

  const loadRequirements = useCallback(
    async (force = false, opts: LoadOptions = {}) => {
      await loadList({
        key: "requirements",
        force,
        setLoading: setRequirementsLoading,
        setError: setRequirementsError,
        path: "/rest/v1/track_training_requirements?select=id,track_id,training_id,is_requirement_active,location_id&order=track_id.asc,training_id.asc",
        mapRow: (r: RequirementRow): Requirement => ({
          id: r.id,
          trackId: r.track_id,
          trainingId: r.training_id,
          active: r.is_requirement_active ?? null,
        }),
        setRows: setRequirements,
        silent: opts.silent,
      });
    },
    [loadList, setRequirements, setRequirementsError, setRequirementsLoading]
  );

  const loadSignoffs = useCallback(
    async (force = false, opts: { silent?: boolean } = {}) => {
      await loadList({
        key: "signoffs",
        force,
        setLoading: setSignoffsLoading,
        setError: setSignoffsError,
        path: "/rest/v1/crew_track_signoffs?select=id,crew_id,track_id,signoff_status,is_signoff_active,location_id&order=crew_id.asc,track_id.asc",
        mapRow: (s: SignoffRow): Signoff => ({
          id: s.id,
          crewId: s.crew_id,
          trackId: s.track_id,
          status: s.signoff_status,
          active: s.is_signoff_active ?? null,
        }),
        setRows: setSignoffs,
        silent: opts.silent,
      });
    },
    [loadList, setSignoffs, setSignoffsError, setSignoffsLoading]
  );

  const loadTrainingRecords = useCallback(
    async (force = false, opts: { silent?: boolean } = {}) => {
      await loadList({
        key: "records",
        force,
        setLoading: setRecordsLoading,
        setError: setRecordsError,
        path: "/rest/v1/v_training_dashboard_with_signer?select=id,crew_id,track_id,training_id,is_record_active,last_completed,training_status,due_date,days_until_due,days_overdue,crew_name,home_department,crew_status,track_name,training_name,last_signed_off_by,last_signed_off_on,location_id&order=crew_name.asc,track_name.asc,training_name.asc",
        mapRow: (r: TrainingRecordRow): TrainingRecord => ({
          id: r.id,
          locationId: r.location_id,
          crewId: r.crew_id,
          trackId: r.track_id,
          trainingId: r.training_id,
          active: !!r.is_record_active,
          lastCompleted: r.last_completed,
          status: r.training_status,
          dueDate: r.due_date,
          daysUntilDue: r.days_until_due,
          daysOverdue: r.days_overdue,
          crewName: r.crew_name,
          homeDepartment: r.home_department,
          trackName: r.track_name,
          trainingName: r.training_name,
          lastSignedOffBy: r.last_signed_off_by,
          lastSignedOffOn: r.last_signed_off_on,
        }),
        setRows: setTrainingRecords,
        silent: opts.silent,
      });
    },
    [loadList, setRecordsError, setRecordsLoading, setTrainingRecords]
  );

  return {
    loadLocations,
    loadDepartments,
    loadCrew,
    loadTracks,
    loadTrainings,
    loadTrainingGroups,
    loadRequirements,
    loadSignoffs,
    loadTrainingRecords,
  };
}
